<li class="form__item form_danger" :class="{ 'form_danger--active': isDeleteHover }"><FormInput class="form_danger" v-if="field === 'subprice'" :label-text="'  '" :placeholder-text="'   '" :input-type="'text'" v-model.number="itemData.value"></FormInput><FormTextarea :placeholder-text="' '" v-if="field === 'subcontent'" v-model="itemData.paragraph"></FormTextarea><FormFieldset v-if="field === 'images'"><teleport v-if="isImageShown" to="#gallery"><img class="form__image" :src="itemData.url"></teleport><FormInput :placeholder-text="'  '" v-model="itemData.url"></FormInput></FormFieldset><FormFieldset v-if="field === 'description'"><FormInput :placeholder-text="' '" v-model.trim="itemData.header"></FormInput><FormMultiBlock v-model="itemData.content" :btn-text="' '" :field="'subcontent'" :isSubdivision="true"></FormMultiBlock></FormFieldset><FormFieldset v-if="field === 'volumes'"><FormInput :label-text="'/   '" :placeholder-text="' /'" :input-type="'text'" v-model.trim="itemData.volume"></FormInput><FormInput :label-text="'   /  '" :placeholder-text="' '" :input-type="'number'" v-model.number="itemData.quantity"></FormInput><FormInput :label-text="''" :placeholder-text="' '" :input-type="'number'" v-model.number="itemData.price"></FormInput><FormMultiBlock v-model="itemData.subprice" :btn-text="'   '" :item-limit="1" :field="'subprice'" :isSubdivision="true"></FormMultiBlock></FormFieldset><FormFieldset v-if="field === 'categories'"><FormSelect v-model="itemData.categoryId" :items="categories" :placeholder-text="'  '"></FormSelect></FormFieldset><FormFieldset v-if="field === 'brands'"><FormSelect v-model="itemData.brandId" :items="brands" :placeholder-text="'  '"></FormSelect></FormFieldset><DeleteButton class="form__close form__item-close" @click="deleteItem" @mouseenter="isDeleteHover = true" @mouseleave="isDeleteHover = false"></DeleteButton></li>